@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face{font-family:'Work Sans';src:url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@100&display=swap")}@font-face{font-family:'Quicksand';src:url("https://fonts.googleapis.com/css2?family=Quicksand&display=swap")}h1,h4,h6{font-family:'Work Sans', sans-serif}p{font-family:'Work Sans', sans-serif}button{font-family:'Work Sans', sans-serif;padding:10px;background:none;border:none;outline:none}button:hover{cursor:pointer}.container{padding:40px}.bold-italics{font-weight:bold;font-style:italic}.navbar{display:flex;justify-content:flex-end;flex-direction:column}.navbar .nav-bold{font-weight:bold}.navbar ul{list-style-type:none;display:flex;justify-content:flex-end}.navbar li{padding:15px}.navbar a{color:black;text-decoration:none}.content{width:50%}.content .post-content{line-height:25px}.content .post-content .center-content{text-align:center}#me{width:75%}.split{display:flex;justify-content:space-between}.left-head{max-width:50%;margin-top:150px}.right-head{margin-top:120px}.right-head p{font-size:25px;font-family:'Quicksand', sans-serif}.right-head p a{text-decoration:none;font-weight:bold;color:black}.right-head p strong{font-style:italic}.skill-tags{display:flex;justify-content:space-evenly;flex-wrap:wrap;padding:30px}.skill-tags span{background-color:lightgray;border-radius:5px;padding:3px;color:black}.resume{width:70%;display:flex;align-self:center;flex-direction:column}.resume .work-element{border-bottom:1px solid black;display:flex}.resume .work-element p{font-size:18px;font-style:normal;line-height:29px}.resume .work-element .right-element{width:60%;display:flex;justify-content:flex-end}.resume .work-element .right-element img{width:70%;vertical-align:middle}.resume .work-element .right-element .image-container{display:flex;justify-content:flex-end}.resume .work-element .left-element{width:40%;padding:3.5rem 2rem 2rem 2rem;box-sizing:border-box}.under-construction{display:flex;justify-content:center;padding-top:100px}.timeline{display:flex;flex-direction:column}.timeline .timeline-header{display:flex;justify-content:center}.timeline .timeline-header .timeline-title{display:flex;flex-direction:column;width:80%}.timeline .timeline-header .timeline-title h2{background:#e6e6e6;border-radius:10px;padding:5px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;align-self:center}.timeline .vertical-timeline-element-icon{overflow:hidden}.timeline .vertical-timeline{max-width:50vw}.timeline .image-container{display:flex;justify-content:center}.timeline .experience-image{max-width:100%}.timeline #data-pipeline{height:420px}@media only screen and (max-width: 990px){.timeline .skill-tags span{margin:5px}}.notebook ul{display:flex;justify-content:space-evenly}@media only screen and (max-width: 500px){.split{flex-direction:column}.left-head{margin-top:50px;max-width:100%}.left-head #me{width:100%;display:flex}.right-head{margin:15px}.navbar nav ul{justify-content:center;padding:0px}.navbar nav li{padding:5px}.resume{width:95%}.resume .work-element{flex-direction:column}.resume .work-element .left-element{width:100%}.resume .work-element .right-element{width:95%;justify-content:center}.resume .work-element .right-element img{width:100%}}

